import type { BiFactory, BiPlugin } from '@ali/4ever-cangjie';
import { isEmptyParagraph } from '@ali/4ever-utils';
import moFactory from '../mo/index';
import onAction from './handlers/onAction';
import createRenderNode from './createRenderNode';
import { queries } from './queries';
import { actions, isActions } from './actions';

const factory: BiFactory = (configs) => {
  const { paragraph = {} } = configs;
  const { locale } = paragraph;

  const plugin: BiPlugin = {
    ...moFactory({}),
    onAction,
    actions,
    isActions,
    queries,
    renderNode: createRenderNode(),
    sidebar: async () => {
      const sidebar: any = await import(/* webpackChunkName: "paragraph_bi_sidebar" */'./sidebar');
      return sidebar.default(configs);
    },
    placeholder: {
      text: locale?.placeholder,
      focusMode: true,
      when: (_, node) => {
        return isEmptyParagraph(node);
      },
    },
  }

  return plugin;
}

export default factory;
